    font-family: "Pacifico";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
    font-family: "Pacifico", monospace;
}

body {
    height: 100vh;
    width: 100vw;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    

    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 20% 60%;
    gap: 5%;
    justify-items: center;
}

#header {
    width: 100vw;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: rgba(22, 20, 20, 0.149);
}

#nav {
    display: flex;
    gap: 30px;
}

#homeBtn, #menuBtn, #aboutBtn {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: none;
    background-color: rgb(231, 243, 61);
    font-size: 18px;
}

#homeBtn:hover, #menuBtn:hover, #aboutBtn:hover {
    transform: scale(1.1);
    filter: brightness(0.85);
}

#homeBtn:active, #menuBtn:active, #aboutBtn:active {
    filter: brightness(0.65);
}

#content {
    display: flex;
    
    height: 100%;
    width: 50%;

    border-radius: 10px;

    background-color: rgba(22, 20, 20, 0.4);
    -webkit-box-shadow: 0px 0px 50px 5px rgba(22,20,20,0.8);
    -moz-box-shadow: 0px 0px 50px 5px rgba(22,20,20,0.8);
    box-shadow: 0px 0px 50px 5px rgba(22,20,20,0.8);
}

#home {
    flex: 1;

    display: flex;
    flex-direction: column;
    justify-content: end;

    text-align: center;
    color: white;
}

#textDiv {
    flex: 1;

    display: flex;
    flex-direction: column;
    row-gap: 2%;

    padding-top: 10%;
    margin-bottom: auto;
}

.color {
    color: rgb(218, 178, 46);
}

#chefDiv {
    flex: 1;
}

#chefImg {
    padding-top: 2%;

    min-width: 50%;
    min-height: 50%;
    max-width: 75%;
    max-height: 75%;
    border-radius: 50%;
}

#menu {
    flex: 1;

    display: grid;
    grid-template-columns: repeat(auto-fit, auto);
    grid-template-rows: repeat(auto-fit, auto);
}

.cake {
    display: grid;
    grid-template-columns: repeat(auto-fit, auto);
    grid-template-rows: repeat(auto-fit, auto);
    padding: 10px 30px;
}

.cakeName {
    grid-row: 1 / 2;
    grid-column: 1 / 2;

    color: rgb(218, 178, 46);
}

.cakeDescription {
    grid-row: 2 / 3;
    grid-column: 1 / 2;

    color: rgb(41, 185, 142);
}

.cakePrice {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    margin-left: auto;
    font-size: 1.2rem;
    color: rgb(138, 218, 46);
}

#about {
    flex: 1;

    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 10%;
    gap: 1.5rem;
}

#aboutTitle {
    color: rgb(218, 178, 46);
    font-size: 2rem;
}

#about > p {
    color: rgb(41, 185, 142);
    font-size: 1.1rem;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,uBAAuB;IACvB,4CAA+C;AACnD;;AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,iBAAiB;IACjB,kCAAkC;AACtC;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,mDAAiD;IACjD,2BAA2B;IAC3B,4BAA4B;IAC5B,sBAAsB;;;IAGtB,aAAa;IACb,2BAA2B;IAC3B,2BAA2B;IAC3B,OAAO;IACP,qBAAqB;AACzB;;AAEA;IACI,YAAY;;IAEZ,aAAa;IACb,mBAAmB;IACnB,uBAAuB;;IAEvB,yCAAyC;AAC7C;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,YAAY;IACZ,mCAAmC;IACnC,eAAe;AACnB;;AAEA;IACI,qBAAqB;IACrB,wBAAwB;AAC5B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,aAAa;;IAEb,YAAY;IACZ,UAAU;;IAEV,mBAAmB;;IAEnB,uCAAuC;IACvC,uDAAuD;IACvD,oDAAoD;IACpD,+CAA+C;AACnD;;AAEA;IACI,OAAO;;IAEP,aAAa;IACb,sBAAsB;IACtB,oBAAoB;;IAEpB,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI,OAAO;;IAEP,aAAa;IACb,sBAAsB;IACtB,WAAW;;IAEX,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,eAAe;;IAEf,cAAc;IACd,eAAe;IACf,cAAc;IACd,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,OAAO;;IAEP,aAAa;IACb,6CAA6C;IAC7C,0CAA0C;AAC9C;;AAEA;IACI,aAAa;IACb,6CAA6C;IAC7C,0CAA0C;IAC1C,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,kBAAkB;;IAElB,wBAAwB;AAC5B;;AAEA;IACI,eAAe;IACf,kBAAkB;;IAElB,wBAAwB;AAC5B;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,iBAAiB;IACjB,iBAAiB;IACjB,wBAAwB;AAC5B;;AAEA;IACI,OAAO;;IAEP,aAAa;IACb,sBAAsB;IACtB,mBAAmB;;IAEnB,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,wBAAwB;IACxB,eAAe;AACnB;;AAEA;IACI,wBAAwB;IACxB,iBAAiB;AACrB","sourcesContent":["@font-face {\n    font-family: \"Pacifico\";\n    src: url(\"./assets/fonts/Pacifico-Regular.ttf\");\n}\n\n* {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n    user-select: none;\n    font-family: \"Pacifico\", monospace;\n}\n\nbody {\n    height: 100vh;\n    width: 100vw;\n    background: url('./assets/images/background.jpg');\n    background-position: center;\n    background-repeat: no-repeat;\n    background-size: cover;\n    \n\n    display: grid;\n    grid-template-columns: 100%;\n    grid-template-rows: 20% 60%;\n    gap: 5%;\n    justify-ite